* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #c0bebe;
  color: #212121;
  line-height: 1.5;

}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.list-item{display: flex;
align-items: center;

}

.list-img{

  width: 80px;
}
.article-title{
margin-left: 10px;

}
.country-list{
  margin: 0;
  padding: 0;
}
.list-container{
  display: flex;
  flex-direction: column;
}
.card-wraper{
  display: flex;
  
}

span{
  font-weight: 900;
}

p{
  font-weight: 400;
}
